import { Injectable } from "@angular/core";
import { Observable, throwError } from "rxjs";
import "rxjs/Rx";
import { HttpClient } from "@angular/common/http";
import { catchError, map } from "rxjs/operators";
import Swal from "sweetalert2";
import { environment } from '../../../../environments/environment';





@Injectable({
  providedIn: "root",
})
export class SavingPigmyAccountClosingService {
  // Variable for handleError
  [x: string]: any;
  // API
  url = environment.base_url;


  constructor(private http: HttpClient) { }
  //Insertion Operation
  postData(data: any): Observable<any> {
    return this.http.post(this.url + "/saving-pigmy-account-closing/insert", data).pipe(
      map((res) => res),
      catchError((error) => {
        Swal.fire("Please Input Proper Data !");
        return throwError(error);
      })
    );
  }
  // For append data
  getFormData(id: any): Observable<any> {
    return this.http
      .get(this.url + "/saving-pigmy-account-closing/" + id)
      .pipe(catchError(this.handleError));
  }
  //Updation Operation
  updateData(data): Observable<any> {
    return this.http.put(this.url + "/saving-pigmy-account-closing/updateClosing", data);
  }
  //Deletion Operation
  deleteData(id: any): Observable<any> {
    return this.http
      .delete(this.url + "/saving-pigmy-account-closing/delete/" + id)
      .pipe(catchError(this.handleError));
  }

  getSchemeCodeList(): Observable<any> {
    return this.http.get(this.url + '/scheme-parameters')
  }

  //get narration data
  getNarrationMaster(): Observable<any> {
    return this.http.get(this.url + '/narration')
  }

  //get syspara 
  getSysParaData(): Observable<any> {
    return this.http.get(this.url + '/system-master-parameters');
  }

  //approve master
  approve(data: any): Observable<any> {
    return this.http.post(this.url + '/saving-pigmy-account-closing/approve', data).pipe(catchError(this.handleError));
  }

  //unapprove master
  unapprove(data: any): Observable<any> {
    return this.http.post(this.url + '/saving-pigmy-account-closing/unapprove', data).pipe(catchError(this.handleError));
  }


  //reject master
  reject(data: any): Observable<any> {
    return this.http.post(this.url + '/saving-pigmy-account-closing/reject', data).pipe(catchError(this.handleError));
  }


}